.page-toolbar-placeholder {
  height: 40px;
  width: 100%;
  margin-bottom: 16px;
  position: relative;
  border-radius: 16px;
  overflow: hidden;

  &::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translateX(-100%);
    animation: shimmer 1s infinite;
    content: '';
  }

  @keyframes shimmer {
    100% {
      transform: translateX(100%);
    }
  }
}

