.video-skeleton-wrapper {
  // flex: 1 1 0;
  height: calc(100vh - 166px);

  .video-skeleton {
    position: relative;
    aspect-ratio: 1 / 1;
    border-radius: 16px;
    overflow: hidden;

    &::after {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      transform: translateX(-100%);
      animation: shimmer 1s infinite;
      content: '';
